import React from "react";
import styled from "styled-components";
import axios from "axios";
import { Link } from "react-router-dom";
async function getDatas(url) {
  const proxyurl = "https://cors-anywhere.herokuapp.com/";
  const seperate = url.split("?")[0];
  const {
    data: { data }
  } = await axios.get(`${proxyurl}${seperate}?`, {
    params: {
      limit: 10
    },
    method: "GET",
    headers: {
      "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
      "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
    }
  });
  return data;
}

export default ({ id, artist }) => {
  // let datas =  getDatas(artist.tracklist);
  // datas.then(res=>datas=res);
  return (
    <Contaier>
      <Poster poster={artist.picture_medium}></Poster>
      <Link
        to={`/artist/${artist.name}`}
        style={{ textDecoration: "none", color: "black" }}
      >
        <Name>{artist.name}</Name>
      </Link>
    </Contaier>
  );
};

const Contaier = styled.div`
  width: 100%;
  font-size: 1.1rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  margin: 0 30px;
`;
const Name = styled.div``;
const Poster = styled.div`
  background-image: url(${props => props.poster});
  width: 70px;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
`;
